@import 'components/variables';

.title-page {
  h1 {
    font-size: 3.5rem;
    font-weight: 800;
    color: $violet;
    margin: 0;
  }
}
.data-search {
  text-align: end;
  .icon {
    width: 1.5rem;
    margin: 0 0.5rem;
  }
}

.consulta-circuito {
  border: 1px solid $text-gray;
  margin-bottom: 2rem;
  .title {
    padding: 1rem;
    background-color: rgba($color: $violet, $alpha: 0.2);
    span {
      font-size: 1.5rem;
      font-weight: 800;
    }
  }
  .form {
    padding: 1rem;
    border-top: 1px solid $text-gray;
    label {
      margin-bottom: 0;
      span {
        color: $violet;
      }
    }
    input, textarea {
      margin-bottom: 1rem;
      border: none;
      border-radius: 0;
      border-bottom: 1px solid $text-gray;
      padding: 0.2rem;
      &:focus {
        box-shadow: none;
      }
    }
    textarea {
      width: 100%;
      resize: none;
      &:focus-visible {
        outline: none;
      }
    }
  }
}

.detalle-circuito {
  .encabezado {
    @media (min-width: 992px) {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    h2 {
      font-size: 2rem;
      font-weight: 800;
    }
    .valoracion {
      text-align: end;
      .valor {
        position: relative;
        background-color: $violet;
        padding: 0.5rem 1rem;
        border-radius: 5px;
        color: white;
        font-size: 1.2rem;
        font-weight: 800;
        margin-right: 1rem;
        &:before {
          position: absolute;
          z-index: -1;
          content: "";
          left: -10px;
          bottom: 0px;
          border-style: solid;
          border-width: 0 10px 10px 10px;
          border-color: transparent transparent $violet transparent;
          transition-duration: .3s;
          transition-property: transform;
        }
      }
    }
  }
}
.location {
  a {
    color: $violet;
    &:hover {
      color: $yellow;
    }
  }
}
.slider-detalle-circuito {
  .slick-slide {
    padding: 0 3px;
    .content-text {
      text-align: center;
      padding: 1rem 2rem;
      display: flex;
      align-items: center;
      color: $gray-light;
      background-color: rgba($color: $violet, $alpha: 0.8);
      h4 {
        font-weight: 800;
      }
    }
    .content-img {
      width: 100%;
      height: 100%;
      overflow: hidden;
      img {
        object-fit: cover;
        object-position: center;
        transition: all 0.5s;
        @media (orientation: landscape) {
          height: 50vh;
        }
        @media (orientation: portrait) {
          height: 50vw;
        }
      }
    }
  }
  .slick-slide.slick-center {
    .content {
      img {
        &:hover {
          transform: scale(1.2);
        }
      }
    }
  }
  .slick-prev, .slick-next {
    color: white;
    font-size: 1rem;
    width: 2rem;
    height: 2rem;
    background-color: rgba($color: $text-gray, $alpha: 0.5);
    z-index: 1;
    &:before {
      display: none;
    }
  }
  .slick-prev {
    left: 0.5rem;
  }
  .slick-next {
    right: 0.5rem;
  }
}
#tabDetalle {
  border-bottom: 1px solid $text-gray;
  .nav-item {
    .nav-link {
      font-size: 1.2rem;
      font-weight: 800;
      color: $text-gray;
      transition: all 0.3s;
      &.active,
      &:hover {
        color: $violet;
      }
      &.active {
        border: 1px solid transparent;
        background-color: transparent;
      }
    }
  }
}
#tabContentDetalle {
  .tab-pane {
    padding: 2rem 1rem;
    .img-itinerario {
      width: 100%;
      height: 30vh;
      object-fit: cover;
      object-position: center;
    }
    .col-text-itinerario {
      border-left: 2px solid $violet;
      position: relative;
      .icon-marker {
        position: absolute;
        top: 1.5rem;
        left: 0;
        transform: translateX(calc(-50% - 1px));
        font-size: 1.3rem;
        color: $violet;
        background-color: white;
      }
      h4 {
        font-weight: 800;
      }
    }
  }
}
hr {
  background-color: $text-gray;
  opacity: 1;
}
.disponibles {
  h3 {
    font-weight: 800;
    margin-bottom: 2rem;
  }
  .img-destacada {
    position: relative;
    border: 1px solid $gray-light;
    padding: 0;
    .content {
      @media (min-width: 768px) {
        position: absolute;
        width: 100%;
        height: 100%;
      }

      @media (max-width: 767.98px) {
        height: 40vw;
      }
      .destacado {
        background-color: $orange;
        text-align: center;
        color: #ffffff;
        font-weight: bold;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
        font-size: .9rem;
        padding: .4rem;
        position: absolute;
        top: 15px;
        left: 0;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }
  }
  .detalles-habitacion {
    border: 1px solid $gray-light;
    padding: 0;
    .valoracion {
      .valor {
        position: relative;
        background-color: $violet;
        padding: 0.5rem 1rem;
        border-radius: 5px;
        color: white;
        font-size: 1.2rem;
        font-weight: 800;
        margin-right: 1rem;
        &:before {
          position: absolute;
          z-index: -1;
          content: "";
          left: -10px;
          bottom: 0px;
          border-style: solid;
          border-width: 0 10px 10px 10px;
          border-color: transparent transparent $violet transparent;
          transition-duration: .3s;
          transition-property: transform;
        }
      }
    }
    .name-habitacion {
      background-color: $gray-light;
      padding: 1rem 1rem;
      border: 1px solid white;
      p {
        font-size: 1.3rem;
        font-weight: 800;
        margin: 0;
      }
    }
    .content {
      padding: 1rem 1rem;
      p {
        margin: 0;
      }
      button {
        margin-top: 1rem;
      }
    }
  }
  .price-habitacion {
    border: 1px solid $gray-light;
    padding: 0;
    .label-precio {
      font-size: 1.5rem;
      margin-bottom: 0;
    }
    .precio {
      font-size: 2rem;
      color: $violet;
      font-weight: 800;
      span {
        font-size: 1.5rem;
        color: $text-gray;
        margin-right: 1rem;
      }
    }
    .descrip-reserva {
      margin-bottom: 0;
      font-weight: 800;
    }
    #seleccionar {
      font-size: 1rem;
      margin-top: 2rem;
    }
  }
}
#modalHabitacion {
  z-index: 3050;
  .modal-content {
    .modal-header {
      background-color: $gray-light;
      .modal-title {
        font-weight: bold;
      }
    }
    .modal-body {
      #carouselHabitacion {
        height: 50vh;
        .carousel-inner {
          height: 100%;
          .carousel-item {
            width: 100%;
            height: 100%;
            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
              object-position: center;
            }
          }
        }
      }
      #tabDetalleHab {
        border-bottom: 1px solid $text-gray;
        .nav-item {
          .nav-link {
            font-size: 1.2rem;
            font-weight: 800;
            color: $text-gray;
            transition: all 0.3s;
            &.active,
            &:hover {
              color: $violet;
            }
            &.active {
              border: 1px solid transparent;
              background-color: transparent;
            }
          }
        }
      }
      #tabContentDetalleHab {
        .tab-pane {
          padding: 2rem 1rem;
          .title-pane {
            display: flex;
            align-items: center;
            margin-bottom: 1rem;
            img {
              width: 3rem;
              margin-right: 1rem;
            }
            h3 {
              font-weight: 800;
            }
          }
          ul {
            padding-left: 1rem;
          }
        }
      }
    }
  }
}